import React, { FC } from 'react';
import { Card, Statistic, Row, Col } from 'antd';
import { Column, Area } from '@ant-design/charts';
import { CaretUpOutlined, CaretDownOutlined, MinusOutlined } from '@ant-design/icons';

export interface DataType {
  title: string;
  dramaId: string;
  commentTotal: number;
}

interface CompareData {
  day: number;
  week: number;
}

interface Props {
  title: string;
  list: any[];
  type: 'area' | 'column';
  topValue: number;
  style?: any;
  yAlias: string;
  compare: CompareData;
}
const areaCardStatisticStyle = { fontSize: '20px', marginBottom: '0' };

const AreaChartCard: FC<Props> = (props: Props) => {
  const { title, list, type, style, yAlias, topValue, compare } = props;
  const config = type === 'area'
    ? {
      data: list,
      height: 150,
      xField: 'time',
      yField: 'count',
      smooth: true,
      meta: {
        time: { alias: '时间' },
        count: { alias: yAlias },
      },
      appendPadding: 10,
      areaStyle: {
        // fill: '#8d65e2',
        fillOpacity: 1,
      }
    }
    : {
      data: list,
      height: 150,
      xField: 'time',
      yField: 'count',
      meta: {
        time: { alias: '时间' },
        count: { alias: yAlias },
      },
      appendPadding: 10,
    };

  const getValueColor = (value: number) => {
    if (value > 0) {
      return '#cf1322';
    }
    if (value < 0) {
      return '#3f8600';
    }
    console.log('value:', value);

    return '#1890ff';
  };

  const getValueIcon = (value: number) => {
    
    
    if (value > 0) {
      return <CaretUpOutlined />;
    }
    if (value < 0) {
      return <CaretDownOutlined />;
    }

    return <MinusOutlined />;
  };

  return (
    <Card size="small" title={ title } style={ style }>
      <Statistic value={ topValue } valueStyle={ areaCardStatisticStyle } />
      {type === 'area'
        ? <Area { ...config } />
        : <Column { ...config } />
      }
      <Row justify="center" gutter={ 24 }>
        <Col>
          <span>周同比</span>
          <Statistic style={{ display: 'inline-block', marginLeft: '5px' }}
            value={ Math.abs(compare?.week || 0) }
            valueStyle={{ color: getValueColor(compare?.week || 0), fontSize: '13px' }}
            prefix={ getValueIcon(compare?.week || 0) } />
        </Col>
        <Col>
          <span>日环比</span>
          <Statistic style={{ display: 'inline-block', marginLeft: '5px' }}
            value={ Math.abs(compare?.day || 0) }
            valueStyle={{ color: getValueColor(compare?.day || 0), fontSize: '13px' }}
            prefix={ getValueIcon(compare?.day || 0) } />
        </Col>
      </Row>
    </Card>
  );
};

export default AreaChartCard;