import React, { FC, useEffect, useState } from 'react';
import { Link } from 'umi';
import { Card, List, Badge } from 'antd';
import { batchGetDramaContentById } from '../../../ContentManage/HotManage/services';
import { ResponseData } from '@/types/types';

export interface DataType {
  title: string;
  dramaId: string;
  commentTotal: number;
}

interface Props {
  title: string;
  extra: string;
  requestAction: any;
  needQueryDetail?: boolean;
  rightKey: string;
}

const PAGE_SIZE = 7;

const DramaListCard: FC<Props> = (props: Props) => {
  const { title, extra, requestAction, needQueryDetail, rightKey } = props;
  const [tableList, setTableList] = useState([]);
  const [total, setTotal] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);

  const getList = (current: number) => {
    requestAction({ current, pageSize: PAGE_SIZE })
      .then((res: ResponseData<any>) => {
        const { code, data } = res;
        
        if (code === 200) {
          const { pagination = {}, list = [] } = data;

          if (needQueryDetail) {
            const dramaIds = list.map((item: any) => item.dramaId);

            if (dramaIds.length === 0) {
              return;
            }
            batchGetDramaContentById(dramaIds).then((batchRes: ResponseData<any>) => {
              if (batchRes.code === 200) {
                const batchObj = batchRes?.data || {};
                
                list.forEach((item: any) => {
                  item.title = batchObj[item.dramaId]?.title;
                });
                setTableList(list);
                setTotal(pagination.total || 0);
                setCurrentPage(current);
              } else {
                setTableList(list);
                setTotal(pagination.total || 0);
                setCurrentPage(current);
              }
            });
          } else {
            setTableList(list);
            setTotal(pagination.total || 0);
            setCurrentPage(current);
          }
        }
      })
      .catch((error: any) => {
        console.error(error);
        setTableList([]);
        setTotal(0);
        setCurrentPage(current);
      });
  };

  useEffect(() => {
    getList(1);
  }, []);

  return (
    <Card size="small" title={ title } extra={ extra }>
      <div style={{ minHeight: '478px' }}>
        <List itemLayout="vertical"
          dataSource={ tableList }
          pagination={ total ? {
            current: currentPage,
            pageSize: PAGE_SIZE,
            onChange: (current: number) => getList(current),
            size: 'small',
            total,
            showSizeChanger: false
          } : false }
          renderItem={ (item: DataType, index: number) => (
            <List.Item>
              <div style={{ padding: '6px 0', display: 'flex', alignItems: 'center' }}>
                <span style={{ display: 'inline-block', width: '20px', textAlign: 'center' }}>
                  {[0, 1, 2].includes(index) && currentPage === 1
                    ? <Badge count={ index + 1 } />
                    : ((currentPage - 1) * PAGE_SIZE) + index + 1}
                </span>
                <Link to={ `/data-manage/data-control?dramaId=${ item.dramaId }` }
                  style={{
                    marginLeft: '20px',
                    width: 'calc(100% - 100px)',
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'nowrap'
                  }}
                >{item.title}
                </Link>
                <span style={{ display: 'inline-block', width: '60px', textAlign: 'right' }}>{item[rightKey] && item[rightKey] > 10000 ? `${ (item[rightKey] / 10000).toFixed(2) }万` : item[rightKey]}</span>
              </div>
            </List.Item>
          ) }
        />
      </div>
    </Card>
  );
};

export default DramaListCard;