import React, { FC, useEffect, useState } from 'react';
import { withAuthorized } from '@/utils/Authorized';
import { Role } from '@/utils/authority';
import { getGrowthTrendList } from './services';
import { Row, Col, Card, Space, DatePicker, Button, Statistic } from 'antd';
import { Column } from '@ant-design/charts';
import moment from 'moment';
import DramaListCard from './components/DramaListCard';
import AreaChartCard from './components/AreaChartCard';
import { ResponseData } from '@/types/types';

import { getCommentTotalList, getFluctuateList, getGrowthList, getFxbData } from './services';

interface TimeType {
  label: string;
  value: string;
}

interface BarDataType {
  time: string;
  count: number;
}

interface FxbData {
  time: string;
  count: number;
}

const testData = [
  { time: moment().subtract(6, 'd').format('YYYY-MM-DD'), count: 3 },
  { time: moment().subtract(5, 'd').format('YYYY-MM-DD'), count: 4 },
  { time: moment().subtract(4, 'd').format('YYYY-MM-DD'), count: 7 },
  { time: moment().subtract(3, 'd').format('YYYY-MM-DD'), count: 3 },
  { time: moment().subtract(2, 'd').format('YYYY-MM-DD'), count: 6 },
  { time: moment().subtract(1, 'd').format('YYYY-MM-DD'), count: 5 },
  { time: moment().format('YYYY-MM-DD'), count: 3 }
];

const { RangePicker } = DatePicker;
const timeTypelist = [
  { label: '12h', value: '12h', timeType: 'hour', unit: 12 },
  { label: '24h', value: '24h', timeType: 'hour', unit: 24 },
  { label: '3天', value: '3d', timeType: 'day', unit: 3 },
  { label: '一周', value: '7d', timeType: 'day', unit: 7 },
  { label: '一个月', value: '30d', timeType: 'day', unit: 30 }
];

const DataStatistics: FC = () => {
  const [currentTimeType, setCurrentTimeType] = useState('12h');
  const [barData, setBarData] = useState<BarDataType[]>([]);
  const [averageNum, setAverageNum] = useState(0);
  const [totalNum, setTotalNum] = useState(0);
  const [dates, setDates] = useState<any>([]);
  const [fxbData, setFxbData] = useState<any>({});
  const baseCol = 3;
  
  const barConfig = {
    data: barData,
    xField: 'time',
    yField: 'count',
    // xAxis: {
    //   label: {
    //     rotate: -Math.PI / 4, // x轴刻度旋转 +顺时针 -逆时针
    //     offset: 30, // 刻度值距离横坐标偏移量
    //   },
    // },
    forceFit: true,
    height: 382,
    label: {
      visible: true,
      style: {
        fill: '#0D0E68',
        fontSize: 12,
        fontWeight: 600,
      },
    },
    meta: {
      time: { alias: '时间' },
      count: { alias: '评论数' },
    },
    // slider: { // x轴展示区间选择
    //   start: 0,
    //   end: 1,
    // }
  };
  /**
   * 
   * @param startTime 开始时间
   * @param endTime 结束时间
   * @param timeType 时间刻度类型 day | hour
   * @param unit 刻度间隔，相隔多少天或多少小时
   */
  const getBarData = (startTime: string, endTime: string, timeType: string, unit: number) => {
    const formatTypeSet = {
      'hour': 'YYYYMMDDHH',
      'day': 'YYYYMMDD',
      'month': 'YYYYMM',
    };

    const diffType = {
      'hour': 'h',
      'day': 'd',
      'month': 'months'
    };
    const formatType = formatTypeSet[timeType];

    getGrowthTrendList({ timeType, startTime, endTime })
      .then((res: any) => {
        if (res.code === 200) {
          const { data: responseList = [] } = res;
          let totalCount = 0;

          responseList.forEach((item: BarDataType) => {
            totalCount += item.count;
          });
          const list: any[] = [];
          const calculateNum = Number((totalCount / unit).toFixed(0));

          setTotalNum(totalCount);
          setAverageNum(calculateNum);

          for (let i = 1; i <= unit; i++) {
          // key为对应时间刻度下的每一个刻度值
            const key = moment(endTime, formatType).subtract(unit - i, diffType[timeType]).format(formatType);

            // 查询当前请求回来数据里面有没有当前时间的数据
            const existData = responseList.find((r: any) => r.time + '' === key);
            // 页面展示的时间格式
            const formatTime = timeType === 'day'
              ? moment(key, formatType).format('YYYY-MM-DD')
              : timeType === 'month'
                ? moment(key, formatType).format('YYYY-MM')
                : `${ moment(key, formatType).format('HH') }时`;

            if (existData) {
              list.push({
                time: formatTime,
                count: existData.count
              });
            } else {
              list.push({
                time: formatTime,
                count: 0
              });
            }
          }
          setBarData(list);
        }
      })
      .catch(() => {
        setBarData([]);
      });
  };

  // 事件类型change
  const changeTimeType = (item: any) => {
    setCurrentTimeType(item.value);
    const { timeType, unit } = item;
    const formatType = timeType === 'day' ? 'YYYYMMDD' : 'YYYYMMDDHH';

    const startTime = moment().subtract(unit - 1, timeType === 'day' ? 'd' : 'h').format(formatType);
    const endTime = moment().format(formatType);
      
    getBarData(startTime, endTime, timeType, unit);
  };
  // 获取周同比和日环比差值
  /**
   * 
   * @param keys ['pv', 'uv', 'loginUv', 'secAvg']
   * @param data 接口数据，以日期为key，包含keys对应的数据
   * @returns 输出keys对应的日环比和周同比差异数据
   */
  const getFxbCompareData = (keys: string[], data: any) => {
    const yesterday = moment().subtract(1, 'd').format('YYYY-MM-DD');
    const dayBeforeYesterday = moment().subtract(2, 'd').format('YYYY-MM-DD');
    // TODO 应该为7
    const lastWeekDay = moment().subtract(7, 'd').format('YYYY-MM-DD');
    const returnData = {};

    keys.forEach((key: string) => {
      const yesData = data[yesterday][key];
      const beforeYesData = data[dayBeforeYesterday][key];
      const beforeWeekYesData = data[lastWeekDay][key];

      returnData[key] = { day: yesData - beforeYesData, week: yesData - beforeWeekYesData };
    });

    return returnData;
  };
  // 获取埋点数据图标的头部数据，平均停留时长求平均值，其余求和
  /**
   * 
   * @param keys ['pv', 'uv', 'loginUv', 'secAvg']
   * @param data 接口数据，以日期为key，包含keys对应的数据
   * @returns 输出keys对应的数据
   */
  const getHeaderData = (keys: string[], data: any[]) => {
    const yesterday = moment().subtract(1, 'd').format('YYYY-MM-DD');
    const returnData = {};

    keys.forEach((key: string, index: number) => {
      const total = 0;

      // data[index].forEach((item: FxbData) => total += item.count);
      returnData[key] = data[index].find((r: FxbData) => r.time === yesterday)?.count;
    });
    
    return returnData;
  };

  useEffect(() => {
    changeTimeType(timeTypelist[0]);
    // 获取埋点数据
    getFxbData().then((res: ResponseData<any>) => {
      const { code, data = {} } = res;

      if (res.code === 0) {
        const timeList: string[] = [];

        for (let i = 6; i >= 0; i--) {
          timeList.push(moment().subtract(i, 'd').format('YYYY-MM-DD'));
        }
        const pvList: FxbData[] = [];
        const uvList: FxbData[] = [];
        const loginUvList: FxbData[] = [];
        const secAvgList: FxbData[] = [];
        const compare = getFxbCompareData(['pv', 'uv', 'loginUv', 'secAvg'], data);

        timeList.forEach((time: string) => {
          pvList.push({ time, count: data[time]['pv'] });
          uvList.push({ time, count: data[time]['uv'] });
          loginUvList.push({ time, count: data[time]['loginUv'] });
          secAvgList.push({ time, count: data[time]['secAvg'] });
        });

        const headerDatas = getHeaderData(['pv', 'uv', 'loginUv', 'secAvg'], [pvList, uvList, loginUvList, secAvgList]);
        

        const finalData = {
          'pv': {
            list: pvList,
            compare: compare['pv'],
            headerData: headerDatas['pv']
          },
          'uv': {
            list: uvList,
            compare: compare['uv'],
            headerData: headerDatas['uv']
          },
          'loginUv': {
            list: loginUvList,
            compare: compare['loginUv'],
            headerData: headerDatas['loginUv']
          },
          'secAvg': {
            list: secAvgList,
            compare: compare['secAvg'],
            headerData: headerDatas['secAvg']
          }
        };

        setFxbData(finalData);
      }
    });
  }, []);
  
  // 时间选择器change事件
  const rangePickerChange = (value: any) => {
    if (value.length) {
      const [startDay, endDay] = value;
      const unit = endDay.diff(startDay, 'months') + 1;
      const startTime = startDay.format('YYYYMM');
      const endTime = endDay.format('YYYYMM');

      setCurrentTimeType('');
      getBarData(startTime, endTime, 'month', unit);
    }
  };

  const disabledDate = (current: any) => {
    if (!dates || dates.length === 0) {
      return false;
    }
    const tooLate = dates[0] && current.diff(dates[0], 'months') > 22;
    const tooEarly = dates[1] && dates[1].diff(current, 'months') > 22;

    return tooEarly || tooLate;
  };

  const numFormat = (number: number) => {
    if (!number) {
      return '0';
    }
    const num = Number(number);

    if (num > 99999999) {
      const formatWan = (num / 100000000).toString();
      const index = formatWan.indexOf('.');

      if (index > -1) {
        return `${ formatWan.slice(0, index + 2) }亿`;
      }

      return `${ formatWan }亿`;
    } else if (num > 9999) {
      const formatWan = (num / 10000).toString();
      const index = formatWan.indexOf('.');

      if (index > -1) {
        return `${ formatWan.slice(0, index + 2) }万`;
      }

      return `${ formatWan }万`;
    }

    return num;
  };

  return (
    <div>
      <Row gutter={ 8 } justify="end">
        <Col span={ 24 }><span style={{ fontSize: '15px', marginBottom: '10px' }}>{moment().subtract(1, 'd').format('YYYY-MM-DD')}</span></Col>
        <Col span={ 6 }>
          <AreaChartCard title="总浏览量(PV)"
            list={ fxbData?.pv?.list || [] }
            type="area"
            topValue={ fxbData?.pv?.headerData || 0 }
            compare={ fxbData?.pv?.compare }
            yAlias="浏览量"
          />
          <AreaChartCard title="用户量"
            list={ fxbData?.loginUv?.list || [] }
            type="column"
            topValue={ fxbData?.loginUv?.headerData || 0 }
            compare={ fxbData?.loginUv?.compare }
            style={{ marginTop: '8px' }}
            yAlias="用户量"
          />
        </Col>
        <Col span={ 6 }>
          <AreaChartCard title="独立访客量(UV)"
            list={ fxbData?.uv?.list || [] }
            type="area"
            topValue={ fxbData?.uv?.headerData || 0 }
            compare={ fxbData?.uv?.compare }
            yAlias="访客量"
          />
          <AreaChartCard title="平均停留时长"
            list={ fxbData?.secAvg?.list || [] }
            type="column"
            topValue={ fxbData?.secAvg?.headerData || 0 }
            compare={ fxbData?.secAvg?.compare }
            style={{ marginTop: '8px' }}
            yAlias="停留时长"
          />
        </Col>
        <Col span={ 6 }>
          <DramaListCard title="作品评分变化排名(24小时)"
            extra="变化分差"
            requestAction={ getFluctuateList }
            needQueryDetail={ true }
            rightKey="publicScoreFluctuate"
          />
        </Col>
        <Col span={ 6 }>
          <DramaListCard title="作品新增评价数排名(24小时)"
            extra="评价条数"
            requestAction={ getGrowthList }
            needQueryDetail={ true }
            rightKey="commentGrowthCount"
          />
        </Col>
        
      </Row>
      <Row gutter={ 8 } style={{ marginTop: '8px' }}>
        <Col span={ 18 }>
          <Card size="small" title="评价增长趋势" extra="评价包括无评论的评分、子评论">
            <Row justify="space-between" style={{ marginBottom: '30px' }}>
              <Col span={ baseCol }>
                <Space direction="vertical">
                  <span>累计评价</span>
                  <span style={{ display: 'flex', alignItems: 'center' }}><span style={{ fontSize: '20px', marginRight: '5px' }}>{numFormat(totalNum)}</span>条</span>
                </Space>
              </Col>
              <Col span={ baseCol }>
                <Space direction="vertical">
                  <span>平均增长评价</span>
                  <span style={{ display: 'flex', alignItems: 'center' }}><span style={{ fontSize: '20px', marginRight: '5px' }}>{numFormat(averageNum)}</span>条</span>
                </Space>
              </Col>
              <Col span={ 24 - baseCol * 2 }>
                <Row justify="space-between" gutter={ 16 }>
                  {timeTypelist.map((item: TimeType) => (
                    <Col key={ item.value }>
                      <Button type={ currentTimeType === item.value ? 'primary' : 'text' }
                        onClick={ () => changeTimeType(item) }
                      >{item.label}
                      </Button>
                    </Col>))}
                  <Col>
                    <RangePicker picker="month"
                      onChange={ rangePickerChange }
                      disabledDate={ disabledDate }
                      onCalendarChange={ (val: any) => setDates(val) } />
                  </Col>
                </Row>
              </Col>
            </Row>
            <Column { ...barConfig } />
          </Card>
        </Col>
        <Col span={ 6 }>
          <DramaListCard title="作品总评价排名"
            extra="评价条数"
            requestAction={ getCommentTotalList }
            rightKey="commentTotal"
          />
        </Col>
      </Row>
    </div>
  );
};

export default withAuthorized(DataStatistics, [Role.SYSTEM, Role.WORKS, Role.COMMENT, Role.EXPERT]);
// export default DataStatistics;
